body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.filter-tools{
  display:-webkit-flex;
  display:flex;
  height:30px;
  -webkit-justify-content: center;
          justify-content: center;
  background:lightgray;
  -webkit-align-items: center;
          align-items: center;
}

#book-type{
  width:200px;
  margin-left:5px;
  
}

#print-type{
  width:100px;
  margin-right:50px;
  margin-left:5px;
}

.search-tool{
  display:-webkit-flex;
  display:flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background:gray;
  height:40px;
}

#search-tool{
  background:rgb(255, 244, 162);
  margin-left:5px;
}

.book-item-container{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin-top:25px;
}

.book-cover{
  display:-webkit-flex;
  display:flex;
  -webkit-align-items: center;
          align-items: center;
  width:20%;
}

li{
  list-style-type:none;
}

#view-more{
  color:blue;
  margin-left: 5px;
}

#view-less{
  color:blue;
  margin-left: 5px;
}

.book-description{
  margin:2em;
}

.book-author{
  margin-bottom: 5px;
}
.App {
  text-align: center;
}



header{
  display:-webkit-flex;
  display:flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color:white;
  background:black;
  height:100px;
}
